.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas: "h h" "n c";

    grid-template-rows: 100px 5fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;

}


.app-wrapper-content {
    grid-area: c;
    background-color: aqua;
    border-radius: 20px;
    box-shadow: 0.4em 0.4em 5px rgba(122,122,122,0.5);

}

